        this.$message.error('ËØ∑ÈÄâÊã©Á≠ñÁï•Áä∂ÊÄÅ')
      }else if(this.addMes.remark==''||this.addMes.remark==null){
        this.$message.error('ËØ∑ËæìÂÖ•Á≠ñÁï•ÊèèËø∞')
      }else{
        this.btnLoading=true;
        this.$http.postFormAPI('mp/strategy',{
          name:this.addMes.name,
          author:this.addMes.cusName,
          style:this.addMes.style,
          risk:Number(this.addMes.type),
          tag:this.addMes.tag,
          expect:this.addMes.get,
          code:this.addMes.sIndex,
          is_lock:Number(this.addMes.state),
          brief:this.addMes.remark
        }).then(res=>{
          this.btnLoading=true;
          if(res.data.state==1){
            this.btnLoading=false;
            this.addStr=false;
            this.$message.success('Ê∑ªÂä†ÊàêÂäü');
            this.getStarList()
          }else{
            this.btnLoading=false;
          }
        }).catch(res=>{
          this.btnLoading=false;
        })
      }
    },
    upLogStr(index){//‰∏ä‰º†ÂéÜÂè≤ÂáÄÂÄº
      this.strID=this.stList[index].id
      this.strUpBox=true;
    },
    getStarList(){//Ëé∑ÂèñÁ≠ñÁï•ÂàóË°®
      this.loadStar=true;
      this.$http.getListAPI('mp/strategy?page='+this.currentPage3+'&limit=15').then(res=>{
        if(res.data.state==1){
          this.loadStar=false;
          this.allPage=res.data.data.pagination.total*5;
          this.stList=res.data.data.list;
          this.stList.forEach((e)=>{
            let tags=e.tag.split(',');
            e.tag=tags;
          });
        }
      })
    },
    upSuc(res){//‰∏ä‰º†ÊàêÂäü
      if(res.state==1){
        this.$message.success('‰∏ä‰º†ÊàêÂäü');
        this.upSuccBack=res.data;
        this.getStarList()
      }else{
        this.$message.error(res.msg)
      }
    },
    upFail(err){//‰∏ä‰º†Â§±Ë¥•
      this.$message.error('ÊÇ®‰∏ä‰º†ÁöÑÊñá‰ª∂ÂÜÖÂÆπÊúâËØØ')
    },
    chanceCode(value){
      this.editMes.code=value;
    }
  }
}
</script>

<style lang="scss" scoped>
.strategyList{
  width: 100%;
  box-sizing: border-box;
  padding: 15px 20px;
  .listTitle{
    width: 100%;
    line-height: 50px;
    font-size: 20px;
    font-weight: bold;
    position: relative;
    button{
      position: absolute;
      right:0;
      top:50%;
      margin-top: -15px;
    }
  }
  .listCon{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
    .conTitle{
      line-height: 40px;
      background:$tabColor;
      color:white;
      text-align: center;
      font-size: 15px;
      font-weight: bold;
    }
    .conMes{
      font-size: 14px;
      line-height: 40px;
      text-align: center;
      .tags{
        height: 26px;
        line-height: 26px;
        padding-left: 5px;
        padding-right: 5px;
        border-radius: 5px;
        background: #eee;
        border:1px solid #ccc;
        margin-right: 5px;
        display: inline-block;
      }
      .operLast{
        color:$tabColor;
        cursor:pointer;

      }
    }
    .stList:nth-of-type(odd){
      background: #eee;
    }
  }
  .buyTitle{
    line-height: 40px;
    text-align: center;
    background: $tabColor;
    color:white;
    font-size: 14px;
    font-weight: bold;
  }
  .buyMes{
    line-height: 40px;
    text-align: center;
    color:black;
  }
  .paperBox{
    width: 100%;
    box-sizing: border-box;
    padding-right: 30px;
    .el-pagination{
      text-align: right;
    }
  }
  .noData{
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
  .upload-demo{
    text-align: center;
  }
  .strDown{
    width: 100%;
    line-height: 40px;
    color:$tabColor;
    text-align: center;
    cursor:pointer;
  }
  .upFinTitle{
    width: 100%;
    color:black;
    font-size: 15px;
    border-bottom: 1px solid #ccc;
    line-height: 30px;
  }
  .finbanckTitle{
    line-height: 40px;
    font-size: 15px;
    font-weight: bold;
    text-align: center;
    color:black;
    border-bottom:1px solid #ccc;
  }
  .finbanckCon{
    line-height: 40px;
    text-align: center;
  }
  .backNoData{
    width: 100%;
    margin-top: 30px;
    font-size: 15px;
    text-align: center;
  }
  .editStrBox{
    width: 100%;
    display: flex;
    ul{
      li{
        line-height: 50px;
      }
    }
    ul:first-child{
      width: 20%;
      text-align: right;
    }
    ul:last-child{
      width: 70%;
      box-sizing: border-box;
      padding-left: 10px;
    }
  }
}
</style>



